function logAction(targetActions, performedAction) {
  let logEntry = createLogEntry(targetActions, performedAction);

  try {
    postLogEntry(logEntry);
  } catch (error) {
    console.log(error);
  }
}

function createLogEntry(targetActions, performedAction) {
  let obj = new Object();
  obj.stepId = 0;
  obj.targetActions = targetActions;
  obj.performedAction = performedAction;

  let action = JSON.parse(JSON.stringify(obj));

  let parent = new Object();
  parent.action = action;
  parent.date = new Date();
  parent.comment = "";
  parent.user = 1;
  return JSON.parse(JSON.stringify(parent));
}

async function postLogEntry(logEntry) {
  try {
    await fetch(process.env.VUE_APP_STRAPI_API_URL + "/api/inf-sortings", {
      method: "POST",
      mode: "cors",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${window.localStorage.getItem("jwt")}`,
      },
      body: JSON.stringify({ data: logEntry }),
    });
  } catch (error) {
    console.log(error)
  }

}

