// 2 = "Gut gemacht! Der Merkzeiger hlt die Karte nun sichtbar und ermglicht Ihnen weiterzuarbeiten!"
// 3 = "Durch das von Ihnen gewhlte systematische Vorgehen, konnten Sie nun alle Karten erfolgreich lesen. Wie geht es nun weiter?"
