<template>
  <div class="cards-container">
    <div v-for="card in activeCardList" :key="card.id">
      <div
        class="sorting-card"
        :class="{ cardFixed: card.fixActive }"
        @drop="dropHandler($event, card)"
        @dragover.prevent
        @dragenter.prevent
      >
        <div v-if="card.isDroppedTo" class="swap-confirmation-dialog">
          <div class="confirmation-header">
            {{ grabbedCard.id + 1 }} <i class="bi bi-arrow-left-right"></i>
            {{ card.id + 1 }}
          </div>
          <div class="confirmation-buttons">
          <div class="confirmation-btn-accept-small" @click="swapConfirmed(card)"><i class="bi bi-check-lg"></i></div>
          <div class="confirmation-btn-cancel-small" @click="card.isDroppedTo = false"><i class="bi bi-x-lg"></i></div>
          </div>
        </div>
        <div v-else>
          <div class="fixieren-container" @click="fixCard(card.id)">
            <p
              class="fix unselectable"
              :class="{ fixNotVisible: !card.fixVisible }"
            >
              FIX
            </p>
          </div>

          <div
            class="number"
            :class="{ readCard: card.cardFlipped }"
            draggable="true"
            @dragstart="dragstartHandler($event, card)"
          >
            {{ !card.cardFlipped ? "" : card.number }}
          </div>

          <div class="btn-container">
            <div
              class="btn unselectable"
              :class="{
                readNotVisible: !card.lesenVisible,
              }"
              @click="readCard(card.id)"
            >
              <EyeIcon :color="card.lesenActive ? 'orange' : 'gray'" />
            </div>
            <div
              class="btn unselectable"
              :class="{
                saveNotVisible: !card.merkenVisible,
              }"
              @click="saveCard(card.id)"
            >
              <PinIcon :color="card.merkenActive ? 'green' : 'gray'" />
            </div>
          </div>
        </div>
      </div>
      <div class="arrow-container">
        <ArrowIcon
          class="read-arrow"
          :color="card.lesenActive ? 'orange' : 'invisible'"
        />
        <ArrowIcon
          class="save-arrow"
          :color="card.merkenActive ? 'green' : 'invisible'"
        />
      </div>
    </div>
  </div>
</template>

<script>
import ArrowIcon from "@/assets/inf/sorting/icons/ArrowIcon.vue";
import EyeIcon from "@/assets/inf/sorting/icons/EyeIcon.vue";
import PinIcon from "@/assets/inf/sorting/icons/PinIcon.vue";

export default {
  components: {
    ArrowIcon,
    EyeIcon,
    PinIcon,
  },
  props: ["tutorialStep"],
  watch: {
    tutorialStep: function (newValue) {
      if (newValue == 1) {
        this.activeCardList = JSON.parse(JSON.stringify(this.cardListStep1));
      }
      if (newValue == 3) {
        this.activeCardList = JSON.parse(JSON.stringify(this.cardListStep3));
      }
      if (newValue == 5) {
        this.activeCardList = JSON.parse(JSON.stringify(this.cardListStep5));
      }
      if (newValue == 7) {
        this.activeCardList = JSON.parse(JSON.stringify(this.cardListStep6));
      }
      if (newValue == 8) {
        this.activeCardList = JSON.parse(JSON.stringify(this.cardListStep8));
      }
    },
  },
  emits: [
    "read-tutorial-finished",
    "swap-tutorial-finished",
    "save-tutorial-finished",
    "fix-tutorial-finished",
  ],
  data() {
    return {
      cardListStep1: [
        {
          id: 0,
          number: 5,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: false,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 1,
          number: 6,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: false,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
      ],
      cardListStep3: [
        //LESEN
        {
          id: 0,
          number: 5,
          lesenVisible: true,
          merkenVisible: false,
          fixVisible: false,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 1,
          number: 6,
          lesenVisible: true,
          merkenVisible: false,
          fixVisible: false,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
      ],
      cardListStep5: [
        //MERKEN
        {
          id: 0,
          number: 5,
          lesenVisible: true,
          merkenVisible: true,
          fixVisible: false,
          swapEnabled: false,
          lesenActive: true,
          merkenActive: false,
          fixActive: false,
          cardFlipped: true,
        },
        {
          id: 1,
          number: 6,
          lesenVisible: true,
          merkenVisible: true,
          fixVisible: false,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
      ],
      cardListStep6: [
        //TAUSCHEN
        {
          id: 0,
          number: 5,
          lesenVisible: true,
          merkenVisible: true,
          fixVisible: false,
          swapEnabled: true,
          lesenActive: true,
          merkenActive: true,
          fixActive: false,
          cardFlipped: true,
          isDroppedTo: false,
        },
        {
          id: 1,
          number: 6,
          lesenVisible: true,
          merkenVisible: true,
          fixVisible: false,
          swapEnabled: true,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
          isDroppedTo: false,
        },
      ],
      cardListStep8: [
        //FIXIEREN
        {
          id: 0,
          number: 2,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: true,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 1,
          number: 5,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: true,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 2,
          number: 6,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: true,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 3,
          number: 7,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: true,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 4,
          number: 9,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: true,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
      ],
      activeCardList: [
        {
          id: 0,
          number: 5,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: false,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 1,
          number: 6,
          lesenVisible: false,
          merkenVisible: false,
          fixVisible: false,
          swapEnabled: false,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
      ],
      grabbedCard: null,
      cardsRead: 0,
      cardsSaved: 0,
      cardsSwapped: 0,
      cardsFixed: 0,
      lastSwapConfirmed: false,
    };
  },
  methods: {
    // Start Drag&Drop functionality
    dragstartHandler(e, card) {
      if (!this.isSwapEnabled(card.id) || this.isCardFixed(card.id)) {
        console.log("swap not allowed");
      } else {
        this.grabbedCard = card;
      }
    },
    dropHandler(e, card) {
      // if (this.isSwapEnabled(card.id) || !this.isCardFixed(this.grabbedCard.id)) {
      e.preventDefault();
      card.isDroppedTo = true;
      // swap cards
      //}
    },
    // End Drag&Drop functionality
    swapConfirmed(card) {
      if (!this.isCardFixed(card.id)) {
        let tmpNumber = card.number;
        card.number = this.grabbedCard.number;
        this.grabbedCard.number = tmpNumber;
        this.checkTutorialsFinished();
      }
      card.isDroppedTo = false;
    },
    // flip cards when read or saved
    flipCard(card) {
      if (card.lesenActive || card.merkenActive) {
        card.cardFlipped = true;
      } else if (!card.lesenActive && !card.merkenActive) {
        card.cardFlipped = false;
      }
    },

    fixCard(cardId) {
      if (this.tutorialStep >= 8) {
        if (!this.isCardFixed(cardId)) {
          const card = this.activeCardList.find((card) => card.id === cardId);
          card.fixActive = !card.fixActive;
          this.checkTutorialsFinished();
        }
      }
    },
    readCard(cardId) {
      if (!this.isCardFixed(cardId)) {
        this.unreadCards();
        const card = this.activeCardList.find((card) => card.id === cardId);
        card.lesenActive = !card.lesenActive;
        this.flipCard(card);
        this.checkTutorialsFinished();
      }
    },
    unreadCards() {
      this.activeCardList.forEach((card) => {
        if (card.lesenActive) {
          card.lesenActive = false;
          this.flipCard(card);
        }
      });
    },
    saveCard(cardId) {
      if (this.isCardRead(cardId) || this.isCardFixed(cardId)) {
        const card = this.activeCardList.find((card) => card.id === cardId);
        this.unsaveCards();
        card.merkenActive = !card.merkenActive;
        this.flipCard(card);
        this.checkTutorialsFinished();
      }
    },
    isCardRead(cardId) {
      return this.activeCardList.find((card) => card.id === cardId).lesenActive;
    },
    unsaveCards() {
      this.activeCardList.forEach((card) => {
        if (card.merkenActive) {
          card.merkenActive = false;
          this.flipCard(card);
        }
      });
    },
    isCardFixed(cardId) {
      return this.activeCardList.find((card) => card.id === cardId).fixActive;
    },
    isSwapEnabled(cardId) {
      return this.activeCardList.find((card) => card.id == cardId).swapEnabled;
    },

    // logic for tutorial

    //check if steps are performed correctly and show corresponding card

    checkTutorialsFinished() {
      switch (this.tutorialStep) {
        case 3:
          this.checkReadTutorialFinished();
          break;
        case 5:
          this.checkSaveTutorialFinished();
          break;
        case 6:
          this.checkSwapTutorialFinished();
          break;
        case 8:
          this.checkFixTutorialFinished();
          break;
      }
    },
    checkReadTutorialFinished() {
      this.cardsRead++;
      if (this.cardsRead >= 2) {
        this.$emit("read-tutorial-finished");
      }
    },
    checkSaveTutorialFinished() {
      this.cardsSaved++;
      if (
        this.activeCardList[0].cardFlipped &&
        this.activeCardList[1].cardFlipped
      ) {
        this.$emit("save-tutorial-finished");
      }
    },
    checkSwapTutorialFinished() {
      this.cardsSwapped++;
      if (this.cardsSwapped >= 1) {
        this.$emit("swap-tutorial-finished");
      }
    },
    checkFixTutorialFinished() {
      this.cardsFixed++;
      if (this.cardsFixed >= 2) {
        this.$emit("fix-tutorial-finished");
      }
    },
  },
};
</script>

<style scoped>
.sorting-card {
  border-radius: 10px;
  border: 3px solid darkgray;
  height: 250px;
  width: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
}

.fixNotVisible {
  visibility: hidden;
}

.readNotVisible {
  visibility: hidden;
}

.saveNotVisible {
  visibility: hidden;
}

.feedback-field {
  border: 1px solid black;
  height: 200px;
  width: 100%;
}

.fixieren-container {
  display: flex;
  cursor: pointer;
}

.cardFixed {
  background-color: #c2ebfa;
}

.cardLesen {
  color: #f28705;
}

.cardMerken {
  color: #1ea983;
}

.fixieren-container:hover {
  text-decoration: underline;
}

.number {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #006bac;
  color: black;
  height: 100px;
  width: 120px;
  border: 4px outset black;
  border-radius: 10px;
  cursor: grab;
}

.number-grabbing {
  cursor: grabbing;
}

.btn-container {
  display: flex;
  justify-content: space-evenly;
}

.btn {
  background-color: white;
  border: 1px solid black;
  border-radius: 10px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin: 10px;
}

.readCard {
  background-color: white;
}

.cards-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  padding: 10px;
  margin: 10px;
}

.swapDisabled {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}
.arrow-container {
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: space-around;
}
.confirmation-dialog{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.confirmation-header{  
  text-align: center;
}

.confirmation-buttons{  
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
