<template>
  <div class="container">
    <div class="assignment-container">
      <div class="assignmentbar" style="sticky">
        <AssignmentbarSorting2
          :showHelpBtn="true"
          :showDismissBtn="true"
          :showFinishBtn="true"
          :sortingFinished="isAssignmentFinished"
          @finish-continue-clicked="$emit('assignment2-finished')"
        ></AssignmentbarSorting2>
      </div>
      <div
        id="card-container"
        @drop="dropCardContainerHandler($event)"
        @dragover.prevent
        @dragenter.prevent
      >
        <div
          class="card"
          id="card1"
          draggable="true"
          @dragstart="dragstartHandler($event, 'card1')"
        >
          Setze LESEZEIGER auf die ganz
          <select
            class="custom-select custom-select-lg mb-3"
            v-model="initReadSelect"
          >
            <option selected>links</option>
            <option>rechts</option>
          </select>
          befindliche nichtfixierte Karte
        </div>
        <div
          class="card"
          id="card2"
          draggable="true"
          @dragstart="dragstartHandler($event, 'card2')"
        >
          Setze MERKZEIGER auf die ganz
          <select
            id="set-merkzeiger-select"
            class="custom-select custom-select-lg mb-3"
            v-model="initSaveSelect"
          >
            <option selected>links</option>
            <option>rechts</option>
          </select>
          befindliche nichtfixierte Karte
        </div>
        <div
          class="card"
          id="card3"
          draggable="true"
          @dragstart="dragstartHandler($event, 'card3')"
        >
          Setze LESEZEIGER eine Position nach
          <select
            id="move-lesezeiger-select"
            class="custom-select custom-select-lg mb-3"
            v-model="moveReadSelect"
          >
            <option selected>links</option>
            <option>rechts</option>
          </select>
        </div>
        <div
          class="card"
          id="card4"
          draggable="true"
          @dragstart="dragstartHandler($event, 'card4')"
        >
          Wenn die Zahl beim LESEZEIGER kleiner ist als die Zahl beim
          MERKZEIGER, dann setze den MERKZEIGER auf die Position des LESEZEIGER.
        </div>
        <div
          class="card"
          id="card5"
          draggable="true"
          @dragstart="dragstartHandler($event, 'card5')"
        >
          Wenn der LESEZEIGER nicht auf der ganz
          <select
            id="position-lesezeiger-select"
            class="custom-select custom-select-lg mb-3"
            v-model="jumpReadSelect"
          >
            <option selected>links</option>
            <option>rechts</option>
          </select>
          befindlichen Karte liegt, springe zu Schritt (3.)
        </div>
        <div
          class="card"
          id="card6"
          draggable="true"
          @dragstart="dragstartHandler($event, 'card6')"
        >
          Vertausche die Karte unter dem MERKZEIGER mit der ganz
          <select
            id="swap-merkzeiger-select"
            class="custom-select custom-select-lg mb-3"
            v-model="swapSaveSelect"
          >
            <option selected>links</option>
            <option>rechts</option>
          </select>
          befindlichen nichtfixierten Karte
        </div>
        <div
          class="card"
          id="card7"
          draggable="true"
          @dragstart="dragstartHandler($event, 'card7')"
        >
          Fixiere die ganz
          <select
            id="fix-select"
            class="custom-select custom-select-lg mb-3"
            v-model="fixCardSelect"
          >
            <option selected>links</option>
            <option>rechts</option>
          </select>
          befindliche nichtfixierte Karte
        </div>
        <div
          class="card"
          :class="grabbedCard === 'card8' ? 'card-grabbed' : ''"
          id="card8"
          draggable="true"
          @dragstart="dragstartHandler($event, 'card8')"
        >
          So lange es nichtfixierte Karten gibt, springe zu Schritt (1.)
        </div>
      </div>
      <div id="algorithm-container">
        <div id="algorithm-list-container">
          <div id="slot-number-container">
            <div class="slot-number" id="">1</div>
            <div class="slot-number" id="">2</div>
            <div class="slot-number" id="">3</div>
            <div class="slot-number" id="">4</div>
            <div class="slot-number" id="">5</div>
            <div class="slot-number" id="">6</div>
            <div class="slot-number" id="">7</div>
            <div class="slot-number" id="">8</div>
          </div>

          <div id="drop-area-container">
            <div
              class="drop-area"
              :class="tickPosition == 1 ? 'drop-area-active' : ''"
              id="drop-slot1"
              @drop="dropSlotHandler($event, 'drop-slot1')"
              @dragover.prevent
              @dragenter.prevent
            ></div>
            <div
              class="drop-area"
              :class="tickPosition == 2 ? 'drop-area-active' : ''"
              id="drop-slot2"
              @drop="dropSlotHandler($event, 'drop-slot2')"
              @dragover.prevent
              @dragenter.prevent
            ></div>
            <div
              class="drop-area"
              :class="tickPosition == 3 ? 'drop-area-active' : ''"
              id="drop-slot3"
              @drop="dropSlotHandler($event, 'drop-slot3')"
              @dragover.prevent
              @dragenter.prevent
            ></div>
            <div
              class="drop-area"
              :class="tickPosition == 4 ? 'drop-area-active' : ''"
              id="drop-slot4"
              @drop="dropSlotHandler($event, 'drop-slot4')"
              @dragover.prevent
              @dragenter.prevent
            ></div>
            <div
              class="drop-area"
              :class="tickPosition == 5 ? 'drop-area-active' : ''"
              id="drop-slot5"
              @drop="dropSlotHandler($event, 'drop-slot5')"
              @dragover.prevent
              @dragenter.prevent
            ></div>
            <div
              class="drop-area"
              :class="tickPosition == 6 ? 'drop-area-active' : ''"
              id="drop-slot6"
              @drop="dropSlotHandler($event, 'drop-slot6')"
              @dragover.prevent
              @dragenter.prevent
            ></div>
            <div
              class="drop-area"
              :class="tickPosition == 7 ? 'drop-area-active' : ''"
              id="drop-slot7"
              @drop="dropSlotHandler($event, 'drop-slot7')"
              @dragover.prevent
              @dragenter.prevent
            ></div>
            <div
              class="drop-area"
              :class="tickPosition == 8 ? 'drop-area-active' : ''"
              id="drop-slot8"
              @drop="dropSlotHandler($event, 'drop-slot8')"
              @dragover.prevent
              @dragenter.prevent
            ></div>
          </div>
        </div>
        <div id="algorithm-button-container">
          <div>Tickposition: {{ tickPosition }}; Tick: {{ tick }}</div>
          <button class="button" @click="tick = true">
            <i class="bi bi-play-btn-fill"></i>
          </button>
          <button class="button" @click="tick = false">
            <i class="bi bi-pause-fill"></i>
          </button>
          <button class="button" @click="stopAlgorithm()">
            <i class="bi bi-stop-fill"> </i>
          </button>
          <button class="button" @click="stepForward()">
            <i class="bi bi-arrow-return-right"> </i>
          </button>
          <button class="button" @click="resetAlgorithm()">
            <i class="bi bi-arrow-counterclockwise"> </i>
          </button>
        </div>
      </div>

      <!--       <div id="number-list-container">
        <div id="number-list-pos1" class="number-list">5</div>
        <div id="number-list-pos2" class="number-list">2</div>
        <div id="number-list-pos3" class="number-list">8</div>
        <div id="number-list-pos4" class="number-list">1</div>
        <div id="number-list-pos5" class="number-list">3</div>
        <div id="number-list-pos6" class="number-list">7</div>
        <div id="number-list-pos7" class="number-list">9</div>
      </div> -->
      <div>
        <ul class="cards-container">
          <li v-for="card in cardList" :key="card.id">
            <transition name="flip">
              <div
                class="number-list"
                :class="{
                  readCard: card.cardFlipped,
                  cardFixed: card.fixActive,
                }"
                :key="card.flipped"
              >
                {{ !card.cardFlipped ? "" : card.number }}
              </div>
            </transition>
            <div class="arrow-container">
              <ArrowIcon
                class="read-arrow"
                :color="card.lesenActive ? 'orange' : 'invisible'"
              />
              <ArrowIcon
                class="save-arrow"
                :color="card.merkenActive ? 'green' : 'invisible'"
              />
            </div>
          </li>
        </ul>
      </div>
      <div id="tracked-parameters">
        <div>ZEIT: {{ elapsedTime }}</div>
        <div>VERSCHIEBEOPERATIONEN: {{ dropActions }}</div>
        <div>RESETS: {{ resets }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import AssignmentbarSorting2 from "@/components/inf/sorting/Assignment2/AssignmentbarSorting2.vue";
import ArrowIcon from "@/assets/inf/sorting/icons/ArrowIcon.vue";

export default {
  components: {
    AssignmentbarSorting2,
    ArrowIcon,
  },
  emits: ["assignment2-finished"],
  mounted() {
    setTimeout(() => {
      window.scrollTo(0, 0);
    });
    //TICKER FOR ALGORITHM --> AUTORUNALGORITHM GETS CALLED EVERY 1 SECOND
    window.setInterval(() => {
      this.autoRunAlgorithm();
    }, 1000);
  },
  data() {
    return {
      isAssignmentFinished: false,
      grabbedCard: "",
      dropActions: 0,
      elapsedTime: -1,
      resets: -1,
      tick: false,
      tickPosition: 1,
      cardsFixed: 0,
      initReadSelect: "",
      initSaveSelect: "",
      moveReadSelect: "",
      jumpReadSelect: "",
      swapSaveSelect: "",
      fixCardSelect: "",
      cardList: [
        {
          id: 1,
          number: 5,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 2,
          number: 4,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 3,
          number: 2,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 4,
          number: 1,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 5,
          number: 3,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
      ],
      resetCardList: [
        {
          id: 1,
          number: 5,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 2,
          number: 4,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 3,
          number: 2,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 4,
          number: 1,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
        {
          id: 5,
          number: 3,
          lesenActive: false,
          merkenActive: false,
          fixActive: false,
          cardFlipped: false,
        },
      ],
    };
  },
  methods: {
    // ---------- DRAG AND DROP FUNCTIONALITY ---------------- //
    dragstartHandler(e, card) {
      this.grabbedCard = card;
    },
    dropCardContainerHandler(e) {
      e.preventDefault();
      document
        .getElementById("card-container")
        .appendChild(document.getElementById(this.grabbedCard));
      this.grabbedCard = "";
    },
    dropSlotHandler(e, cardSlot) {
      e.preventDefault();
      if (
        cardSlot.includes("drop-slot") &&
        !this.checkIfDropSlotContainsCard(cardSlot)
      ) {
        e.target.appendChild(document.getElementById(this.grabbedCard));
      }
      this.grabbedCard = "";
    },
    checkIfDropSlotContainsCard(dropSlot) {
      let parent = document.getElementById(dropSlot);
      if (parent.childNodes.length >= 1) {
        return true;
      }
      return false;
    },
    increaseDropActions() {
      this.dropActions = this.dropActions + 1;
    },

    // ---------- BASIC ALGORITHM METHODS ---------------- //
    autoRunAlgorithm() {
      if (this.tick) {
        this.stepForward();
      }
    },
    stopAlgorithm() {
      this.tick = false;
      this.tickPosition = 1;
      this.cardList = this.resetCardList;
    },
    pauseAlgorithm() {
      this.tick = false;
    },
    stepForward() {
      let parent = document.getElementById("drop-slot" + this.tickPosition);
      if (parent.childNodes.length <= 0) {
        this.tick = false;
        alert("Algorithmus ist leer!");
        return;
      }
      let child = parent.childNodes[0];
      this.doStep(child.id);
    },
    doStep(cardId) {
      switch (cardId) {
        case "card1":
          this.moveReadToFirst();
          this.increaseAlgoStep();
          break;
        case "card2":
          this.moveSaveToFirst();
          this.increaseAlgoStep();
          break;
        case "card3":
          this.moveReadToNext();
          this.increaseAlgoStep();
          break;
        case "card4":
          this.moveSaveToRead();
          this.increaseAlgoStep();
          break;
        case "card5":
          this.jumpToStep3();
          break;
        case "card6":
          this.swapSaveWithFirst();
          this.increaseAlgoStep();
          break;
        case "card7":
          this.fixSortedCard();
          this.increaseAlgoStep();
          break;
        case "card8":
          this.jumpToStep1();
          break;
        default:
          alert("Something went wrong");
      }
    },
    //------------------- SORTING LIST HELPER METHODS -------------- //
    getCard(cardId) {
      return this.cardList.find((card) => card.id === cardId);
    },
    increaseAlgoStep() {
      this.tickPosition = (this.tickPosition % 8) + 1;
    },
    flipCard(card) {
      if (card.lesenActive || card.merkenActive) {
        card.cardFlipped = true;
      } else if (!card.lesenActive && !card.merkenActive) {
        card.cardFlipped = false;
      }
    },
    unreadAllCards() {
      this.cardList.forEach((card) => {
        if (card.lesenActive) {
          card.lesenActive = false;
          this.flipCard(card);
        }
      });
    },
    unsaveAllCards() {
      this.cardList.forEach((card) => {
        if (card.merkenActive) {
          card.merkenActive = false;
          this.flipCard(card);
        }
      });
    },

    // CARD 1
    moveReadToFirst() {
      //console.log("moveReadToFirst");
      if (!this.initReadSelect) {
        alert("keine Richtung gesetzt");
        this.stopAlgorithm();
        return;
      }
      let firstUnfixedCard = this.getFirstUnfixedCard(this.initReadSelect);
      this.unreadAllCards();
      this.cardList[firstUnfixedCard].lesenActive = true;
      this.cardList[firstUnfixedCard].cardFlipped = true;
    },
    getFirstUnfixedCard(direction) {
      //console.log(direction);
      if (direction === "links") {
        if (!this.cardList[0].fixActive) return 0;
        else if (!this.cardList[1].fixActive) return 1;
        else if (!this.cardList[2].fixActive) return 2;
        else if (!this.cardList[3].fixActive) return 3;
        else if (!this.cardList[4].fixActive) return 4;
      } else if (direction === "rechts") {
        if (!this.cardList[4].fixActive) return 4;
        else if (!this.cardList[3].fixActive) return 3;
        else if (!this.cardList[2].fixActive) return 2;
        else if (!this.cardList[1].fixActive) return 1;
        else if (!this.cardList[0].fixActive) return 0;
      }
    },
    //CARD 2
    moveSaveToFirst() {
      //console.log("moveSaveToFirst");
      if (!this.initSaveSelect) {
        alert("Keine Richtung gesetzt!");
        this.stopAlgorithm();
        return;
      }
      let firstUnfixedCard = this.getFirstUnfixedCard(this.initSaveSelect);
      this.unsaveAllCards();
      this.cardList[firstUnfixedCard].merkenActive = true;
      this.cardList[firstUnfixedCard].cardFlipped = true;
    },
    //CARD 3
    moveReadToNext() {
      //console.log("moveReadToNext");
      if (!this.moveReadSelect) {
        alert("Keine Richtung gesetzt!");
        this.stopAlgorithm();
        return;
      }
      let positionCurrentReadCard = this.getPositionCurrentReadCard();
      if (positionCurrentReadCard == -1) {
        this.stopAlgorithm();
        alert("noch keine Karte gelesen");
        return;
      }
      let nextReadCard = this.getNextReadCard(
        positionCurrentReadCard,
        this.moveReadSelect
      );
      if (nextReadCard == 5) {
        return;
      }
      //console.log("nextReadCard: " + nextReadCard);
      this.unreadAllCards();
      this.cardList[nextReadCard].lesenActive = true;
      this.flipCard(this.cardList[positionCurrentReadCard]);
      this.flipCard(this.cardList[nextReadCard]);
    },
    getPositionCurrentReadCard() {
      for (let i = 0; i < this.cardList.length; i++) {
        if (this.cardList[i].lesenActive) return i;
      }
      return -1;
    },
    getPositionCurrentSaveCard() {
      for (let i = 0; i < this.cardList.length; i++) {
        if (this.cardList[i].merkenActive) return i;
      }
      return -1;
    },
    getNextReadCard(positionCurrentReadCard, direction) {
      if (direction === "links")
        return (positionCurrentReadCard - 1) % (this.cardList.length - 1);
      else if (direction === "rechts") return positionCurrentReadCard + 1;
    },
    moveSaveToRead() {
      //console.log("moveSaveToRead");
      let positionReadCard = this.getPositionCurrentReadCard();
      if (positionReadCard == -1) {
        this.stopAlgorithm();
        alert("noch keine Karte gelesen");
        return;
      }
      let positionSaveCard = this.getPositionCurrentSaveCard();
      if (positionSaveCard == -1) {
        this.stopAlgorithm();
        alert("noch keine Karte gemerkt");
        return;
      }
      let currentReadValue = this.cardList[positionReadCard].number;
      let currentSaveValue = this.cardList[positionSaveCard].number;
      //console.log('currentReadValue: ' + currentReadValue + " currentSaveValue: " + currentSaveValue )
      if (currentReadValue < currentSaveValue) {
        this.unsaveAllCards();
        this.cardList[positionReadCard].merkenActive = true; //move merkzeiger to position of read card
        this.flipCard(this.cardList[positionSaveCard]); //unflip old saved card
      }
    },
    jumpToStep3() {
      //console.log("jumpToStep3");
      let positionReadCard = this.getPositionCurrentReadCard();
      if (positionReadCard == -1) {
        this.stopAlgorithm();
        alert("noch keine Karte gelesen");
        return;
      }
      let positionSaveCard = this.getPositionCurrentSaveCard();
      if (positionSaveCard == -1) {
        this.stopAlgorithm();
        alert("noch keine Karte gemerkt");
        return;
      }

      if (this.jumpReadSelect === "links") {
        if (!(positionReadCard == 0)) {
          this.tickPosition = 3;
        } else {
          this.increaseAlgoStep();
        }
      } else if (this.jumpReadSelect === "rechts") {
        //console.log('position read card' + positionReadCard)
        if (!(positionReadCard == 4)) {
          this.tickPosition = 3;
        } else {
          this.increaseAlgoStep();
        }
      } else {
        alert("keine richtung ausgewhlt");
        this.stopAlgorithm();
        return;
      }
    },
    swapSaveWithFirst() {
      //console.log("swapSaveWithFirst");
      if (!this.swapSaveSelect) {
        alert("Keine Richtung gesetzt!");
        this.stopAlgorithm();
        return;
      }
      let positionFirstUnfixedCard = this.getFirstUnfixedCard(
        this.swapSaveSelect
      );
      let positionSaveCard = this.getPositionCurrentSaveCard();
      if (positionSaveCard == -1) {
        this.stopAlgorithm();
        alert("noch keine Karte gemerkt");
        return;
      }

      let tmpNumber = this.cardList[positionFirstUnfixedCard].number;
      //console.log(tmpNumber + " >?<" + this.cardList[positionSaveCard].number)
      this.cardList[positionFirstUnfixedCard].number =
        this.cardList[positionSaveCard].number;
      this.cardList[positionSaveCard].number = tmpNumber;
    },
    fixSortedCard() {
      //console.log("fixSortedCard");
      if (!this.fixCardSelect) {
        alert("Keine Richtung gesetzt! fix select");
        this.stopAlgorithm();
        return;
      }
      let firstUnfixedCard = this.getFirstUnfixedCard(this.fixCardSelect);
      this.cardList[firstUnfixedCard].fixActive = true;
      this.cardsFixed++;
    },
    checkSortedFinished() {
      for (let i = 0; i < this.cardList.length - 1; i++) {
        if (!(this.cardList[i].number < this.cardList[i + 1].number))
          return false;
      }
      return true;
    },
    jumpToStep1() {
      if (this.cardsFixed == 5) {
        if (this.checkSortedFinished()) {
          this.isAssignmentFinished = true;
          this.tick = false;
          alert("assignment finished");
        } else {
          this.tick = false;
          this.tickPosition = 0;
          alert("nicht richtig sortiert");
        }
      } else {
        this.tickPosition = 1;
      }
      //console.log("jumpToStep1");
    },
  },
};
</script>

<style scoped>
.button {
  background-color: #41d946;
  border: none;
  border-radius: 5px;
  color: black;
  padding: 5px 5px;
  text-align: center;
  text-decoration: none;
  margin: 10px 4px;
  cursor: pointer;
  width: 100px;
  height: 60px;
}

.button:hover {
  background-color: #1d441f;
  color: white;
}

#card-container {
  background-color: rgb(245, 245, 245);
  border: 2px solid darkgray;
  padding: 3px;
  margin: 10px;
}

.card {
  border: 2px solid black;
  background-color: white;
  cursor: move;
  display: inline-block;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
  font-size: 1.1em;
  width: 32%;
}

.card-grabbed {
  background-color: red;
}

#algorithm-container {
  display: flex;
  justify-content: center;
  align-items: stretch;
}

#algorithm-list-container {
  display: flex;
  flex-grow: 8;
  margin: 20px;
}

#slot-number-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.slot-number {
  height: 60px;
  margin: 3px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 1.3em;
}

#drop-area-container {
  display: flex;
  flex-direction: column;
  flex-grow: 7;
  width: 1000px;
}

.drop-area {
  background-color: rgb(85, 153, 255);
  border-radius: 5px;
  height: 60px;
  margin: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.drop-area-active {
  border: 2px solid black;
}

#algorithm-button-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

#number-list-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.number-list {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  height: 100px;
  width: 120px;
  border: 2px solid black;
  border-radius: 10px;
  cursor: grab;
}

input {
  font-size: 1.1em;
  width: 50px;
  text-align: center;
