<template>
  <div class="container mb-2 pb-2">
    <div class="button-bar assignment-bar">
      <!-- Button trigger modal -->
      <div class="button-bar-left">
        <div
          v-if="showHelpBtn"
          type="button"
          @click="openHelpModal"
          class="custom-btn-secondary dismiss-btn"
          data-bs-toggle="modal"
          data-bs-target="#modal"
        >
          <i class="bi bi-question-circle"></i>Hilfe
        </div>
      </div>
      <div class="button-bar-right">
        <div
          v-if="showDismissBtn"
          type="button"
          @click="openQuitModal"
          class="custom-btn-secondary dismiss-btn"
          data-bs-toggle="modal"
          data-bs-target="#modal"
        >
          Bearbeitung abbrechen
        </div>
        <div
          v-if="showFinishBtn"
          type="button"
          @click="openFinishModal"
          class="custom-btn dismiss-btn"
          data-bs-toggle="modal"
          data-bs-target="#modal"
        >
          Weiter
        </div>
      </div>
    </div>

    <BaseModal>
      <template v-slot:header>
        <div class="modal-title">
          <!-- help modal title -->
          <div v-if="showModal === 'help'">Vorgaben und Regeln</div>
          <!-- quit modal title -->
          <div v-else-if="showModal === 'quit'">Aufgabe abbrechen</div>
          <!-- finish modal title -->
          <div v-else-if="showModal === 'finish'">Sind Sie fertig?</div>
          <div v-else>Error</div>
        </div>
      </template>

      <template v-slot:body>
        <!-- help modal body -->
        <div v-if="showModal === 'help'">
          <p>Und hier noch einmal alle Vorgaben und Regeln in Krze:</p>
          <ul>
            <li>
              Die Karten sollen nach der Gre ihres Wertes sortiert werden -
              links der kleinste, rechts der grte Wert.
            </li>
            <li>
              Der Lesezeiger macht den Wert einer verdeckten Karte sichtbar.
            </li>
            <li>
              Die Werte verdeckter Karten sind unbekannt, auch wenn sie schon
              einmal gelesen wurden.
            </li>
            <li>Der Merkzeiger hlt eine Karte sichtbar.</li>
            <li>Die Karten werden durch Drag &#38; Drop getauscht.</li>
            <li>
              Mindestens eine Karte muss nach einem Tausch auf ihrem endgltigen
              Platz liegen.
            </li>
            <li>Die sortierten Karten werden zum Abschluss fixiert.</li>
            <li>
              Die Sortierung soll in mglichst wenigen Schritten erfolgen.
            </li>
          </ul>
        </div>
        <!-- quit modal body -->
        <div v-else-if="showModal === 'quit'">
          <p>
            Wollen Sie die Sortieraufgabe abbrechen? Bitte beachten Sie, dass
            Sie keine Rckmeldung zum OSA Teil Sortieren mit System! bekommen
            werden, wenn Sie die Bearbeitung der Aufgabe jetzt abbrechen.
          </p>
          <p>
            Falls Sie dennoch abbrechen mchten, teilen Sie uns bitte Ihren
            Grund mit:
          </p>
          <form>
            <div class="form-group">
              <textarea class="form-control" id="message-text"></textarea>
            </div>
          </form>
        </div>
        <!-- finish modal body -->
        <div v-else-if="showModal === 'finish'">
          <div v-if="sortingFinished">
            <p>
              Sehr gut! Sie haben alle Zahlen nun in aufsteigender Reihenfolge
              sortiert!
            </p>
            <p>
              Zum Abschluss bitten wir Sie, einige Fragen zum Experiment zu
              beantworten.
            </p>
          </div>
          <div v-else>
            <p>
              Sie haben die Aufgabe noch nicht bis zur Lsung bearbeitet. Wollen
              Sie an dieser Stelle abbrechen?
            </p>
            <p>
              Bitte beachten Sie, dass Sie keine Rckmeldung zum OSA Teil
              Sortieren mit System! bekommen werden, wenn Sie die Bearbeitung
              der Aufgabe jetzt abbrechen.
            </p>
            <p>
              Falls Sie dennoch abbrechen mchten, teilen Sie uns bitte Ihren
              Grund mit:
            </p>

            <form>
              <div class="form-group">
                <textarea class="form-control" id="message-text"></textarea>
              </div>
            </form>
          </div>
        </div>
        <div v-else>Error loading text</div>
      </template>

      <template v-slot:footer>
        <div v-if="showModal === 'help'">
          <button type="button" class="btn custom-btn" data-bs-dismiss="modal">
            OK
          </button>
        </div>
        <!-- quit modal -->
        <div v-else-if="showModal === 'quit'">
          <button
            type="button"
            class="btn custom-btn-danger"
            data-bs-dismiss="modal"
            @click="endAssignment"
          >
            Aufgabe abbrechen
          </button>
          <button type="button" class="btn custom-btn" data-bs-dismiss="modal">
            Aufgabe fortsetzen
          </button>
        </div>
        <!-- finish modal -->
        <div v-else-if="showModal === 'finish'">
          <div v-if="sortingFinished">
            <button
              type="button"
              class="btn custom-btn"
              data-bs-dismiss="modal"
              @click="$emit('finish-continue-clicked')"
            >
              Weiter
            </button>
          </div>
          <div v-else>
            <button
              type="button"
              class="btn custom-btn-danger"
              data-bs-dismiss="modal"
              @click="endAssignment"
            >
              Aufgabe abbrechen
            </button>
            <button
              type="button"
              class="btn custom-btn"
              data-bs-dismiss="modal"
            >
              Aufgabe fortsetzen
            </button>
          </div>
        </div>
        <div v-else>
          <button type="button" class="btn custom-btn" data-bs-dismiss="modal">
            Schlieen
          </button>
        </div>
      </template>
    </BaseModal>
  </div>
</template>

<script>
import BaseModal from "@/components/BaseModal.vue";

export default {
  name: "AssignmentbarSorting1",
  components: {
    BaseModal,
  },
  data() {
    return {
      showModal: "",
    };
  },
  props: {
    showHelpBtn: Boolean,
    showDismissBtn: Boolean,
    showFinishBtn: Boolean,
    sortingFinished: Boolean,
  },
  emits: ["end-assignment", "skip-tutorial", "finish-continue-clicked"],
  methods: {
    openHelpModal() {
      this.showModal = "help";
    },
    openQuitModal() {
      this.showModal = "quit";
    },
    openFinishModal() {
      this.showModal = "finish";
    },
    endAssignment() {
      let messageText = document.getElementById("message-text").value;
      if (messageText.length > 1) {
        this.saveQuitMessage(messageText);
      }

      this.$emit("end-assignment");
      this.$router.push("/informatik");
    },
    async saveQuitMessage(messageText) {
      try {
        await fetch(
          process.env.VUE_APP_STRAPI_API_URL +
            "/api/inf-sorting-quit-messages",
          {
            method: "POST",
            mode: "cors",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${window.localStorage.getItem("jwt")}`,
            },
            body: JSON.stringify({
              data: {
                message: messageText,
              },
            }),
          }
        );
      } catch (error) {
        console.log(error);
      }
    }
  },
};
</script>

<style scoped>
