          </div>
        </div>
      </div>

      <div class="slide" v-else-if="pageNumber === 5">
        <div class="slide-left-container">
          <h3 class="slide-left-container-header">MERKEN</h3>
          <div class="slide-left-container-content">
            <div class="btn"><PinIcon :color="'green'" /></div>
          </div>
        </div>
        <div class="slide-text-container">
          <div class="main-text">
            Mit einem Klick auf das Hilfsmittel <strong>Merken</strong> knnen
            Sie den Wert einer sichtbaren Karte sichtbar halten, auch wenn der
            Lesezeiger nicht mehr auf diese Karte gerichtet ist. Wird der
            Merkzeiger auf eine andere Karte gesetzt, steht der Wert nicht mehr
            zur Verfgung. Setzen Sie nun den Merkzeiger auf die Karte, deren
            Wert Sie sehen und stellen Sie anschlieend den Lesezeiger auf eine
            andere Karte. Wie Sie sehen, knnen Sie durch die Kombination der
            Hilfsmittel <strong>Lesen</strong> und <strong>Merken</strong> die Werte zweier Karten sichtbar
            machen.
          </div>
          <div class="additional-text">
            Wir Menschen merken uns viele Dinge - ob wir wollen oder nicht. Ein
            Computerprogramm merkt sich nur Dinge, die ihm explizit vorgeben
            werden. Daher sind die Werte verdeckter Karten fr ein
            Computerprogramm immer unbekannt. Fr den Vergleich von Zahlenwerten
            werden jedoch zwei sichtbare Karten bentigt. Mit Hilfe des
            Merkzeigers ist es jetzt mglich, zwei Kartenwerte gleichzeitig
            sehen zu knnen.
          </div>
          <div class="error-text" v-html="errorMessage"></div>
        </div>
      </div>

      <div class="slide" v-else-if="pageNumber === 6">
        <div class="slide-left-container"><SwapIcon /></div>
        <div class="slide-text-container">
          <div class="main-text">

            Um die Reihenfolge der Karten zu ndern, tauschen Sie jeweils zwei Karten durch Klicken und Ziehen mit der Maus (Drag &#38; Drop). Versuchen Sie es! Klicken Sie auf eine der beiden Karten mit sichtbarem Wert, halten Sie die Maustaste gedrckt und ziehen Sie die Karte auf jene, mit der Sie tauschen mchten. Den Tausch schlieen Sie ab, indem Sie auf das grne Hkchen klicken. Der Klick auf x bricht den Tausch ab.

          </div>
          <div class="additional-text"></div>
          <div class="error-text" v-html="errorMessage"></div>
        </div>
      </div>

      <div class="slide" v-else-if="pageNumber === 7">
        <div class="slide-left-container"></div>
        <div class="slide-text-container">
          <div class="main-text">
            <p>Aber beachten Sie:</p>
            <p></p>
            <p>
Werden zwei Karten in ihrer Position verndert (= Sortierschritt), muss zumindest eine der beiden Karten nach Abschluss der Tauschoperation an ihrem endgltigen Platz zu liegen kommen.
            </p>
          </div>
          <div class="additional-text">
          </div>
        </div>
      </div>
      <div class="slide" v-else-if="pageNumber === 8">
        <div class="slide-left-container"><FixIcon /></div>
        <div class="slide-text-container">
          <div class="main-text">
            <strong>Und zuletzt:</strong> Mit dem <strong>Hilfsmittel Fixieren</strong> grenzen
            Sie den Bereich bereits sortierter Zahlen vom Bereich noch zu
            sortierender Zahlen ab. Klicken Sie dazu auf das Wort FIX auf dem
            jeweiligen Platz. Mit jeder sortierten Karte muss der Fixierbereich
            erweitert werden. Im fixierten Bereich sind dann auch keine
            Operationen mehr mglich. Versuchen Sie, die Aufgabe mglichst
            effizient, d. h. mit wenigen Schritten zu lsen.
          </div>
          <div class="additional-text">
Damit das Programm nicht vergisst, welche Zahlen schon sortiert sind und wo noch zu sortieren ist, verwendet es die Fixierfunktion. 

          </div>
          <!-- <div class="error-text" v-html="errorMessage"></div> -->
        </div>
      </div>
      <!--<div class="slide" v-else-if="pageNumber === 8">
        <div class="slide-left-container"></div>
        <div class="slide-text-container">
          <div class="main-text">
            Die Sortierung ist abgeschlossen, wenn die zu sortierenden Karten
            auf ihren richtigen Pltzen liegen.
          </div>
          <p><br /></p>
          <div>
            Und hier noch einmal alle Vorgaben und Regeln in Krze:
            <ul>
              <li>
                Die Karten sollen nach der Gre ihres Wertes sortiert werden -
                links der kleinste, rechts der grte Wert.
              </li>
              <li>
                Der Lesezeiger macht den Wert einer verdeckten Karte sichtbar.
              </li>
              <li>
                Die Werte verdeckter Karten sind unbekannt, auch wenn sie schon
                einmal gelesen wurden.
              </li>
              <li>Der Merkzeiger hlt eine Karte sichtbar.</li>
              <li>Die Karten werden durch Drag &#38; Drop getauscht.</li>
              <li>
                Mindestens eine Karte muss nach einem Tausch auf ihrem
                endgltigen Platz liegen.
              </li>
              <li>Die sortierten Karten werden zum Abschluss fixiert.</li>
              <li>
                Die Sortierung soll in mglichst wenigen Schritten erfolgen.
              </li>
            </ul>
            Klicken Sie auf Start, um mit der Aufgabe zu beginnen.
          </div>
          <div class="additional-text"></div>
        </div>
      </div>-->
    </div>

    <div class="navigation-container">
      <div class="navigation">
        <div class="prev" @click="previousPage">&#10094;</div>
        <span class="page-number">{{ pageNumber }}</span>
        <span v-for="i in pages" :key="i"
          ><div class="dot" :class="{ 'active-dot': pageNumber >= i }"></div
        ></span>
        <span class="page-number">8</span>
        <div
          class="next"
          :class="{ hidenext: pageNumber == 8 }"
          @click="nextPage"
        >
          &#10095;
        </div>
      </div>
      <div @click="skipTutorial" class="skip-tutorial" v-if="pageNumber != 8">
        Anleitung berspringen
      </div>
      <div @click="skipTutorial" class="skip-tutorial" v-if="pageNumber == 8">
        Start
      </div>
    </div>
  </div>
</template>

<script>
import CardsTutorial from "@/components/inf/sorting/Assignment1/Tutorial/CardsTutorial.vue";
import EyeIcon from "@/assets/inf/sorting/icons/EyeIcon.vue";
import PinIcon from "@/assets/inf/sorting/icons/PinIcon.vue";
import SwapIcon from "@/assets/inf/sorting/icons/SwapIcon.vue";
import FixIcon from "@/assets/inf/sorting/icons/FixIcon.vue";

export default {
  components: {
    EyeIcon,
    PinIcon,
    SwapIcon,
    FixIcon,
    CardsTutorial,
  },
  data() {
    return {
      pageNumber: 1,
      pages: 8,
      readFinished: false,
      saveFinished: false,
      fixFinished: false,
      swapFinished: false,
      errorMessage: "",
    };
  },
  emits: ["tutorial-finished"],
  mounted() {
    setTimeout(() => {
      window.scrollTo(0, 0);
    });
  },
  methods: {
    nextPage() {
      //check if read tutorial is finished
      if (this.pageNumber == 3 && !this.readFinished) {
        //set error message
        this.errorMessage =
          "Klicken Sie nacheinander auf das Auge unter den beiden Karten. Sie knnen fortsetzen, wenn Sie beide Karten gelesen haben!";
        return;
      }
      //check if save tutorial is finished
      if (this.pageNumber == 5 && !this.saveFinished) {
        //show error message
        this.errorMessage =
          "Um zur nchsten Seite zu gelangen, machen Sie zwei Karten sichtbar und klicken Sie dann auf den blauen Pfeil unten <em>rechts</em>.";
        return;
      }
      if (this.pageNumber == 7 && !this.swapFinished) {
        //show error message
        this.errorMessage =
          "Um zur nchsten Seite zu gelangen, nehmen Sie zumindest einen Tausch vor und klicken Sie dann auf den blauen Pfeil unten <em>rechts</em>.";
        return;
      }
      if (this.pageNumber == 9) {
        this.$emit("tutorial-finished");
        return;
      }
      this.pageNumber++;
      this.pageNumber = Math.min(this.pages, this.pageNumber);
      this.errorMessage = "";
    },
    previousPage() {
      this.pageNumber--;
      this.pageNumber = Math.max(1, this.pageNumber);
    },
    readTutorialFinished() {
      this.readFinished = true;
    },
    saveTutorialFinished() {
      this.saveFinished = true;
    },
    fixTutorialFinished() {
      this.fixFinished = true;
    },
    swapTutorialFinished() {
      this.swapFinished = true;
    },
    skipTutorial() {
      this.$emit("tutorial-finished");
    },
  },
};
</script>

<style scoped>
