  border: 1px solid black;
  border-radius: 10px;
  width: 20%;
  display: flex;
  flex-direction: column;
}

.slide-left-container-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.additional-text {
  padding-top: 10px;
  font-style: italic;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
}

.error-text {
  padding-top: 10px;
  font-weight: bold;
  color: red;
}

/* ----- NAVIGATION ------ */
.navigation-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}

.navigation {
  flex: 0 1 auto;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.prev,
.next {
  cursor: pointer;
  width: auto;
  padding: 25px;
  font-weight: bolder;
  font-size: 40px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
  color: #006bac;
}

.hidenext {
  visibility: hidden;
}

/* The dots/bullets/indicators */
.dot {
  height: 5px;
  width: 10px;
  margin: 0 2px;
  background-color: #006bac;
  opacity: 0.3;
  transition: background-color 0.6s ease;
}

.active-dot {
  background-color: #006bac;
  opacity: 1;
}

.skip-tutorial {
  cursor: pointer;
  flex: 0 1 auto;
  margin-left: auto;
}

.skip-tutorial:hover {
  text-decoration: underline;
}

.instruction {
  font-weight: bold;
}

.btn {
  background-color: white;
  border: 1px solid black;
  border-radius: 10px;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin: 10px;
}
</style>
