        nicht berschreibt. berlegen Sie kurz, was das fr eine groe Menge
        bzw. eine kleine Menge von Elementen bedeutet. In der Regel sind
        In-Place Verfahren langsamer als out-of-place Verfahren, bentigen aber
        nur wenig zustzlichen Speicher.
